import React, {useEffect} from 'react'
import s from "../../../CreateTableConfiguration.module.css"
import {makeStyles} from "@material-ui/core/styles";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import clsx from "clsx";

const Step3 = (props) => {

    useEffect(() => {
        window.parent.postMessage({message: 'unhighlight_table', content: ''}, '*')
    }, [])

    const headers = props.headers

    const handleInputChange = (e) => {
        props.updateCheckboxStatus(e.target.id)
    }

    const useStyles = makeStyles({
        root: {
            '&:hover': {
                backgroundColor: 'transparent',
            },
            padding: '7px 16px'
        },
        icon: {
            borderRadius: 1,
            width: 14,
            height: 14,
            boxShadow: 'inset 0 0 0 1px rgba(16,22,26,.2), inset 0 -1px 0 rgba(16,22,26,.1)',
            backgroundColor: '#fff',
            //backgroundImage: 'linear-gradient(180deg,hsla(0,0%,100%,.8),hsla(0,0%,100%,0))',
            '$root.Mui-focusVisible &': {
                outline: '2px auto rgba(19,124,189,.6)',
                outlineOffset: 2,
            },
            'input:hover ~ &': {
                backgroundColor: '#ebf1f5',
            },
            'input:disabled ~ &': {
                boxShadow: 'none',
                background: 'rgba(206,217,224,.5)',
            },
        },
        checkedIcon: {
            backgroundColor: '#1976D2',
            //backgroundImage: 'linear-gradient(180deg,hsla(0,0%,100%,.1),hsla(0,0%,100%,0))',
            '&:before': {
                display: 'block',
                width: 14,
                height: 14,
                backgroundImage:
                    "url(\"data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3E%3Cpath" +
                    " fill-rule='evenodd' clip-rule='evenodd' d='M12 5c-.28 0-.53.11-.71.29L7 9.59l-2.29-2.3a1.003 " +
                    "1.003 0 00-1.42 1.42l3 3c.18.18.43.29.71.29s.53-.11.71-.29l5-5A1.003 1.003 0 0012 5z' fill='%23fff'/%3E%3C/svg%3E\")",
                content: '""',
            },
            'input:hover ~ &': {
                backgroundColor: '#106ba3',
            },
        },
    });
    function StyledCheckbox(props) {
        const classes = useStyles();

        return (
            <FormControlLabel
                control={
                    <Checkbox
                        className={classes.root}
                        disableRipple
                        color="default"
                        checkedIcon={<span className={clsx(classes.icon, classes.checkedIcon)} />}
                        icon={<span className={classes.icon} />}
                        inputProps={{ 'aria-label': 'decorative checkbox' }}
                        {...props}
                    />
                }
                checked={props.isClicked}
                onChange={handleChange}
                label={props.label}
            />
        );
    }

    const handleChange = (event) => {
        props.updateCheckboxStatus(event.target.id)
        //setState({ ...state, [event.target.name]: event.target.checked });
    }


    return (
        <div className={s.generalStepContainer}>
            <p className={s.pageName}>Mark cells</p>
            <p className={s.stepNumber}>Step 3 of 3</p>
            <div className={s.stepContainer}>
                <p className={s.notes}>Select required table cells (not selected cells will not be considered on the next step).</p>
                {
                    Array.isArray(headers) ? (
                        <div className={s.headersContainer}>
                            {
                                headers.map((item, index) => {
                                    headers.map((item, index) => {
                                        return <StyledCheckbox id={index} label={item.name} checked={item.isClicked} onChange={handleChange} />
                                    })
                                })
                            }
                        </div>
                    ) : (
                        <div>no headers</div>
                    )
                }
            </div>
            <div className={s.navButtons}>
                <button onClick={props.prev} className={s.prevButton}>Previous</button>
                <button onClick={props.next} className={s.nextButton}>Next</button>
            </div>
        </div>
    )
}

export default Step3
